___CSS_LOADER_EXPORT___.push([module.id, `/*************************************************************************
 * Design System CSS File
 **************************************************************************/

/* Font Import */

/* Color Palette */
:root {
    --primary-dark: #171923;
    --secondary-dark: #212330;
    --main-yellow: #EEBE43;
    --second-yellow: #CBA543;
    --white-color: #FFFFFF;
}

/* Global Setting */
body {
    margin: 0;
    padding: 0;
    font-family: "Inter", sans-serif;
    background-color: var(--primary-dark);
}

/* Custom Scroll */
::-webkit-scrollbar {
    width: 12px;
    background-color: var(--secondary-dark);
  }
  
  ::-webkit-scrollbar-thumb {
    background-color: var(--main-yellow);
    border-radius: 5px;
  }

  /* Skip to Content */
  .skip_link {
    position: absolute;
    top: -40px;
    left: 0;
    background-color: var(--main-yellow);
    color: var(--primary-dark);
    font-weight: 700;
    padding: 8px;
    z-index: 100;
  }

  .skip_link:focus{
    top: 0;
  }

  #loading_text {
    color: var(--main-yellow);
    font-size: 1.5rem;
