********\n * Main CSS File\n **************************************************************************/\n\n/* Import Design System CSS */\n@import url('design_sys.css');\n\n\n/* Header - Start */\n#navbar_laperzz {\n  width: 100%;\n  height: 75px;\n  background-color: var(--secondary-dark);\n  display: flex;\n  flex-direction: row;\n  align-items: center;\n  justify-content: space-between;\n}\n\n/* Not Found Route */\n.not-found {\n  color: var(--white-color);\n  font-weight: bold;\n  display: flex;\n  justify-content: center;\n  margin-top: 2rem;\n}\n\n#navbar_logo {\n  margin-left: 80px;\n}\n\n.navlist {\n  display: flex;\n  gap: 25px;\n  margin-right: 80px;\n}\n\n.nav_item {\n  list-style: none;\n}\n\n.nav_item>a {\n  text-decoration: none;\n  color: var(--white-color);\n  font-weight: bold;\n  font-size: 16px;\n}\n\n.nav_item>a:hover {\n  color: var(--main-yellow);\n}\n\n.nav_item>a:active {\n  color: var(--second-yellow);\n}\n\n/* Mobile Ver */\n#navbar_laperzz_mobile {\n  width: 100%;\n  height: 75px;\n  background-color: var(--secondary-dark);\n  display: flex;\n  flex-direction: row;\n  align-items: center;\n  justify-content: space-between;\n}\n\n#navbar_logo_mob {\n  margin-left: 50px;\n\n}\n\n#hamburger_button {\n  margin-right: 40px;\n  background-color: transparent;\n  border: none;\n  color: var(--main-yellow);\n  font-size: 1.5rem;\n  cursor: pointer;\n  height: 44px;\n  width: 44px;\n}\n\n#mob_drawer {\n  width: 250px;\n  height: 100%;\n  position: fixed;\n  left: 0;\n  display: flex;\n  flex-direction: column;\n  z-index: 99;\n\n  transform: translate(-250px, 0);\n  transition: transform 0.3s ease-in-out;\n  background-color: var(--secondary-dark);\n}\n\n#mob_drawer.open {\n  transform: translate(0, 0);\n}\n\n.mob_list {\n  list-style-type: none;\n  padding: 0 24px;\n}\n\n.mob_item {\n  padding: 5px 0;\n  margin-bottom: 2rem;\n}\n\n.mob_item>a {\n  text-decoration: none;\n  color: var(--white-color);\n  font-weight: bold;\n  font-size: 20px;\n  text-align: center;\n  padding: 10px 20px;\n}\n\n.mob_item>a:hover {\n  color: var(--main-yellow);\n}\n\n/* Header - End */\n/* ********************************* */\n/* Main - Start */\n\n/* Hero Elemet Section */\n\n#hero_element {\n  display: flex;\n  flex-direction: column;\n  align-items: center;\n  justify-content: center;\n  height: 400px;\n  width: 100%;\n\n  background: linear-gradient(0deg, rgba(0, 0, 0, 0.55) 0%, rgba(0, 0, 0, 0.55) 100%),\n    lightgray url('../public/images/heros/hero-image_2.jpg') no-repeat center / cover;\n}\n\n#hero_element>h1 {\n  color: var(--white-color);\n  font-size: 48px;\n  font-style: normal;\n  font-weight: 700;\n  line-height: normal;\n}\n\n#hero_element>h2 {\n  color: var(--main-yellow);\n  font-size: 32px;\n  font-style: italic;\n  font-weight: 700;\n  line-height: normal;\n  margin-top: -2.5rem;\n}\n\n/* Resto Banner Section */\n#resto_banner>h1 {\n  color: var(--main-yellow);\n  font-size: 48px;\n  font-style: normal;\n  font-weight: 700;\n  line-height: normal;\n}\n\n#resto_banner>h2 {\n  color: var(--white-color);\n  font-size: 32px;\n  font-style: normal;\n  font-weight: 700;\n  line-height: normal;\n  margin-top: -2.5rem;\n}\n\n/* Explore Resto Section */\n\n#explore_resto {\n  display: flex;\n  flex-direction: column;\n  align-items: center;\n  justify-content: center;\n  width: 100%;\n  margin-top: 100px;\n}\n\n#explore_resto>h2 {\n  color: var(--white-color);\n  font-size: 36px;\n  font-weight: 700;\n  line-height: normal;\n  margin-bottom: 60px;\n}\n\n.wrapper_resto {\n  display: grid;\n  grid-template-columns: repeat(3, minmax(400px, 1fr));\n  grid-template-rows: repeat(auto-fit, 400px);\n  gap: 25px;\n  align-items: center;\n  justify-content: center;\n}\n\n.resto_item {\n  background-color: var(--secondary-dark);\n  height: 400px;\n  width: 400px;\n  border-radius: 10px;\n}\n\n.resto_item:hover {\n  transform: scale(1.1);\n}\n\n\n.resto_item:focus {\n  transform: scale(1.1);\n}\n\n.img_resto {\n  max-width: 400px